<template>
	<view class="aqk-item">
		<view class="aqk-item-content">
			<view class="aqk-item-title" @click="passionActive">
				<text>{{item.title}}</text>
			</view>
			<view class="aqk-item-description">
				<text>{{item.desc}}</text>
			</view>
			<view class="aqk-item-author">
				<uni-icons class="icons" type="personidea" size="20" color="#b8b8b8"></uni-icons>
				<view class="aqk-item-source" :class="{verify: item.author.user_label==='verify'}">{{item.author.nickname}}</view>
				<image v-if="item.author.user_label==='verify'" src="https://p0.ssl.qhimg.com/sdm/45_45_100/t01d1216d1bce6e01cc.png"></image>
			</view>
			<view class="aqk-item-post-time">{{item.date}}</view>
		</view>
		<view class="aqk-item-img">
			<image :src="item.cover" mode="aspectFill"></image>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			item: {
				type: Object,
				default () {
					return {}
				}
			}
		},
		data() {
			return {
				baseAvatar: 'https://img1.doubanio.com/lpic/s28050969.jpg',
				aSheetList: [{
					text: "收集"
				}, {
					text: "删除"
				}],
				showAS: false
			};
		},
		onLoad() {
		},
		methods: {
			passionActive() {
				uni.navigateTo({
					url: '/pages/anquanke/detail?anquanke='
					+ 'https://www.anquanke.com/post/id/' 
					+ this.item.id
				})
			},
			tapAuthor() {
				// uni.navigateTo({
				// 	url: "/pages/world/world?connectTheWorld=" + "https://www.freebuf.com/author/" + this.item.username
				// })
			},
			activeAS() {
				setTimeout(() => {
					this.showAS = true
				}, 1000);
			},
			colOrdel(index) {
				console.log(index)
				if (index === 0) {
					this.showTips(this.item.post_title, "success")
				} else {
					this.showTips(this.item.post_title, "warning")
				}
			},
			showTips(title, type) {
				this.$refs.uTips.show({
					duration: 4000,
					title: title,
					type: type
				});
			}
		},

	};
</script>

<style lang="scss">
	.aqk-item {
		display: flex;
		padding: 20upx;
		margin: 20upx;
		border-bottom: solid 2upx #f0f0f0;
		box-sizing: border-box;
		flex-direction: row;

		.aqk-item-img {
			flex-shrink: 0;
			width: 288upx;
			height: 200upx;
			overflow: hidden;
			margin: auto;

			image {
				width: 100%;
				height: 100%;
			}

			.tag {
				position: absolute;
				left: 50upx;
				margin-top: 10upx;
				line-height: 1;
				padding: 10upx 20upx;
				color: #fff;
				font-size: 24upx;
				border-radius: 4upx;
				background-color: #19a358;
			}
		}

		.aqk-item-content {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			padding-left: 0;
			width: 60%;

			.aqk-item-title {
				font-size: 32upx;
				color: #333;
				line-height: 48upx;
				margin: 0;
				font-weight: bold;
				padding-right: 20upx;

				text {
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}
			}

			.aqk-item-description {
				font-size: 28upx;
				color: #333;
				font-weight: 400;
				line-height: 1.6;
				padding-right: 60upx;
				margin-top: 20upx;

				text {
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					color: #999;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					font-size: 24upx;
					padding-top: 0upx;
				}
			}
			
			.aqk-item-author {
				display: flex;
				flex-direction: row;
				padding-top: 16upx;
				
				image {
					width: 26upx;
					height: 26upx;
					margin-left: 10upx;
					align-self: center;
				}
				
				.aqk-item-source {
					font-size: 26upx;
					color: #333;
					line-height: 1;
					align-self: center;
					// align-self: flex-end;
					&.verify {
						color: #CC6633 !important;
					}
				}
				
				.icons {
					padding-top: 1upx;
					margin-right: 12upx;
					align-self: center;
				}
			}
				
			.aqk-item-post-time {
				color: #b8b8b8;
				font-size: 23upx;
				line-height: 1.5;
				right: 0;
				padding-top: 16upx;
			}
		}
	}
</style>


